<template>
  <ul id="picUl">
    <li class="list" v-for="p in pic" :key="p.id">
      <img class="enlarge" :src="p.src" alt="" @click="openPic(p.id)" />
      <div>
        <h1>{{ p.title }}</h1>
      </div>
    </li>
    <div id="mask" v-show="isShow"></div>
    <img v-show="isShow" :src="pic[num].src" alt="" id="img1" class="img" />
    <img
      id="btn"
      @click="closeImg"
      v-show="isShow"
      src="../../assets/background/img-insert/close.png"
      alt=""
    />
  </ul>
</template>

<script>
export default {
  name: "ThePicture",
  methods: {
    openPic(num) {
      this.num = num - 1;
      this.isShow = true;
      this.$bus.$emit("changeClose");
    },
    closeImg() {
      this.isShow = false;
      this.$bus.$emit("changeClose");
    },
  },
  data() {
    return {
      isShow: false,
      num: 0,
      pic: [
        {
          id: 1,
          src: require("@/assets/graphic/bg1.png"),
          title: "图片测试01",
        },
        {
          id: 2,
          src: require("@/assets/graphic/bg2.png"),
          title: "图片测试02",
        },
        {
          id: 3,
          src: require("@/assets/graphic/bg3.png"),
          title: "图片测试03",
        },
        {
          id: 4,
          src: require("@/assets/graphic/bg4.png"),
          title: "图片测试04",
        },
        {
          id: 5,
          src: require("@/assets/graphic/bg5.png"),
          title: "图片测试05",
        },
        {
          id: 6,
          src: require("@/assets/graphic/bg6.png"),
          title: "图片测试06",
        },
      ],
    };
  },
};
</script>

<style scoped>
#picUl {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.list {
  /* float: left; */
  width: 350px;
  height: 300px;
  /* overflow: hidden; */
  margin: 40px;
  background-color: rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 5px 2px white;
  border-radius: 5px;
}
.list img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  border-radius: 5px;
  /* box-shadow: 0px 0px 5px 2px skyblue; */
}
.list div {
  text-align: center;
}

#btn {
  position: absolute;
  right: 9.5%;
  top: 16%;
  /* right: -38%;
  top: -14%; */
  width: 50px;
  height: 50px;
  z-index: 10;
}

#btn:hover {
  cursor: pointer;
}

#mask {
  position: absolute;
  left: -500px;
  top: -100px;
  height: 145%;
  width: 300%;
  opacity: 0.6;
  z-index: 0;
  background-color: black;
}
.enlarge:hover {
  transform: scale(1.1);
  cursor: pointer;
}
.img {
  position: absolute;
  left: 50%;
  top: 63%;
