<template>
  <div class="box1">
    <img
      id="btn"
      src="../../assets/background/img-insert/close.png"
      @click="close"
      v-show="btnShow"
    />
    <div id="background" v-show="isShow">
      <ThePicture></ThePicture>
    </div>
  </div>
</template>

<script>
import ThePicture from "../BgInGraphic/ThePicture";
export default {
  name: "TheGraphic",
  data() {
    return {
      isShow: true,
      btnShow: true,
    };
  },
  methods: {
    close() {
      this.isShow = !this.isShow;
    },
  },
  components: {
    ThePicture,
  },
  mounted() {
    this.$bus.$on("changeClose", () => {
      this.btnShow = !this.btnShow;
    });
  },
};
</script>

<style scoped>
.box1 {
  height: 1200px;
  background-image: url("@/assets/graphic/main-pic.bmp");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 100%;
  background-attachment: fixed;
  overflow: hidden;
  /* margin-bottom: -200px; */
}

#background {
  margin: 100px auto;
  width: 1300px;
  /* height: 800px; */
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 5px;
  box-shadow: 0px 0px 5px 2px grey;
  z-index: 0;
}

#background:after {
  /*伪元素是行内元素 正常浏览器清除浮动方法*/
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
#btn {
  position: absolute;
  right: 7.7%;
  top: 13.5%;
  width: 30px;
  height: 30px;
  z-index: 10;
}

#btn:hover {
  cursor: pointer;
}
</style>